<template>
    <div class="container">
        <div v-show="!gameHasEnded" class="game_container">
            <div class="my_number" ref="my_number">
                <h1>{{ number || "####" }}</h1>
            </div>
            <div class="my_tries">
                <div
                    class="try"
                    v-for="(item, index) in myTries"
                    :key="index"
                    :ref="index"
                >
                    <div class="digits_container">
                        <div
                            v-for="(digit, index_digit) in item.digits"
                            :class="`digit ${digit[1]}`"
                            :key="digit[0]"
                            @click.stop="changeState(index, index_digit)"
                        >
                            {{ digit[0] }}
                        </div>
                    </div>
                    <div class="response_container">
                        <div class="correct_container">
                            <p class="correct_number">{{ item.correct }}</p>
                            <span class="material-symbols-rounded">
                                check
                            </span>
                        </div>
                        <div class="positioned_container">
                            <p class="positioned_container">
                                {{ item.positioned }}
                            </p>
                            <span class="material-symbols-rounded">
                                done_all
                            </span>
                        </div>
                    </div>
                </div>
            </div>
        </div>
        <div
            v-show="!gameHasEnded"
            class="input_container"
            ref="input_container"
        >
            <input
                @keydown="eraser"
                @keypress="writer"
                autofocus
                maxlength="4"
                placeholder="Type Your Number"
                type="number"
                v-model="inputValue"
                v-if="turn"
            />
            <LoaderComponent reduced="true" v-else />
        </div>
        <div v-show="gameHasEnded" class="announcer_container">
            <h1>{{ winner ? "YOU WIN! üèÜ" : "YOU LOSE! ü•≤" }}</h1>
            <div
                @click.stop="$router.replace({ name: 'start' })"
                class="back_btn"
            >
                HOME
            </div>
        </div>
    </div>
</template>
<script>
import socketIO from "@/socket";
import LoaderComponent from "./LoaderComponent.vue";

export default {
    beforeUnmount() {
        if (!this.gameHasEnded && this.started) {
            this.socket.emit("game_abbandoned");
            this.$router.replace({ name: "start" });
        }
    },
    created() {
        this.socket = socketIO;
        this.socket.emit("request_number", (number) => {
            this.number = number;
        });
        this.socket.on("you_win", () => {
            this.gameHasEnded = true;
            this.winner = true;
        });
        this.socket.on("you_lose", () => {
            this.gameHasEnded = true;
            this.winner = false;
        });
    },
    components: {
        LoaderComponent,
    },
    data() {
        return {
            gameHasEnded: false,
            inputValue: "",
            myTries: [],
            number: null,
            socket: null,
            winner: true,
        };
    },
    methods: {
        changeState(index, index_digit) {
            const changer = document.createElement("div");

            changer.classList.add("changer_container");

            changer.style.alignItems = "center";
            changer.style.backdropFilter = "blur(10px)";
            changer.style.display = "flex";
            changer.style.flexDirection = "row";
            changer.style.height = "100%";
            changer.style.justifyContent = "center";
            changer.style.left = "0";
            changer.style.position = "fixed";
            changer.style.top = "0";
            changer.style.width = "100%";
            changer.style.zIndex = "2";

            changer.innerHTML = `
				<ul style="list-style: none;width: max-content;">
					<li id="normal_opt" style="display: flex;flex-direction: row;align-items: center;justify-content: center; background-color: #151515;margin: 10px 0;border-radius: 15px;padding: 10px;font-size: 1.5em;">
						Normal
					</li>
					<li id="correct_opt" style="display: flex;flex-direction: row;align-items: center;justify-content: center; background-color: rgb(69, 65, 190);margin: 10px 0;border-radius: 15px;padding: 10px;font-size: 1.5em;">
						Correct
					</li>
					<li id="positioned_opt" style="display: flex;flex-direction: row;align-items: center;justify-content: center; background-color: rgb(80, 184, 80);margin: 10px 0;border-radius: 15px;padding: 10px;font-size: 1.5em;">
						Positioned
					</li>
					<li id="wrong_opt" style="display: flex;flex-direction: row;align-items: center;justify-content: center; background-color: rgb(184, 60, 60);margin: 10px 0;border-radius: 15px;padding: 10px;font-size: 1.5em;">
						Wrong
					</li>
				</ul>
			`;

            changer
                .querySelector("#normal_opt")
                .addEventListener("click", (e) => {
                    e.stopPropagation();
                    e.preventDefault();
                    this.myTries[index].digits[index_digit][1] = "normal";
                    changer.remove();
                });
            changer
                .querySelector("#correct_opt")
                .addEventListener("click", (e) => {
                    e.stopPropagation();
                    e.preventDefault();
                    this.myTries[index].digits[index_digit][1] = "correct";
                    changer.remove();
                });
            changer
                .querySelector("#positioned_opt")
                .addEventListener("click", (e) => {
                    e.stopPropagation();
                    e.preventDefault();
                    this.myTries[index].digits[index_digit][1] = "positioned";
                    changer.remove();
                });
            changer
                .querySelector("#wrong_opt")
                .addEventListener("click", (e) => {
                    e.stopPropagation();
                    e.preventDefault();
                    this.myTries[index].digits[index_digit][1] = "wrong";
                    changer.remove();
                });

            document.getElementById("app").appendChild(changer);
        },
        request() {
            this.socket.emit(
                "new_try",
                this.inputValue,
                ({ correct, positioned }) => {
                    this.myTries.unshift({
                        number: this.inputValue,
                        digits: this.inputValue
                            .split("")
                            .map((e) => [e, "normal"]),
                        correct,
                        positioned,
                    });
                    this.inputValue = "";
                    this.$emit("end_turn");
                }
            );
        },
        writer(e) {
            e.stopPropagation();
            e.preventDefault();
            this.inputValue = String(this.inputValue);
            const { charCode, key } = e;
            if (
                47 < charCode &&
                charCode < 58 &&
                !this.inputValue.includes(key) &&
                this.inputValue.length < 4
            )
                this.inputValue += key;
            else if (charCode === 13) {
                if (this.inputValue.length !== 4) this.invalidToast();
                else this.request();
            }
        },
        eraser(e) {
            if (e.keyCode === 8) {
                e.stopPropagation();
                e.preventDefault();
                this.inputValue = this.inputValue.slice(
                    0,
                    this.inputValue.length - 1
                );
            } else return;
        },
        invalidToast() {
            const oldToast = document
                .getElementById("app")
                .querySelector(".toast");
            if (oldToast) oldToast.remove();

            const toast = document.createElement("div");

            toast.classList.add("toast");

            toast.innerHTML = `
				<span class="material-symbols-rounded toast_icon">error</span>
				<h3 class="toast_text">Invalid Number</h3>
				<div class="toast_timer"></div>
			`;

            document.getElementById("app").appendChild(toast);
            toast.style.left = `${
                document.getElementById("app").offsetWidth / 2 -
                toast.offsetWidth / 2
            }px`;
            setTimeout(() => {
                toast.remove();
            }, 1500);
        },
    },
    mounted() {
        const inputHeight = this.$refs.input_container.offsetHeight;
        const myNumberHeight = this.$refs.my_number.offsetHeight;
        this.$el.style.setProperty("--input_height", `${inputHeight}px`);
        this.$el.style.setProperty("--my_number_height", `${myNumberHeight}px`);
    },
    name: "GameComponent",
    props: {
        started: Boolean,
        turn: Boolean,
    },
    watch: {
        turn(newVal) {
            if (newVal) {
                this.$refs.input_container.focus();
            }
        },
    },
};
</script>

<style scoped lang="scss">
.container {
    height: 100%;
    position: relative;
    width: 100%;

    .game_container {
        height: 100%;
        padding: 20px 30px 0;
        width: 100%;

        .my_number {
            background-color: #151515;
            border-radius: 10px;
            font-size: 1.5em;
            padding: 10px 30px;
            width: fit-content;
            margin: 0 auto;
        }

        .my_tries {
            height: calc(100% - var(--my_number_height) - var(--input_height));
            overflow-x: hidden;
            overflow-y: scroll;
            padding: 20px 0 0;
            width: 100%;

            .try {
                margin: 0 0 30px;

                .digits_container {
                    align-items: center;
                    display: flex;
                    flex-direction: row;
                    justify-content: center;
                    margin-bottom: 10px;

                    .digit {
                        align-items: center;
                        background-color: #151515;
                        border-radius: 30%;
                        cursor: pointer;
                        display: flex;
                        font-size: 1.3em;
                        font-weight: bolder;
                        height: 35px;
                        justify-content: center;
                        margin: 0 10px;
                        width: 35px;

                        &.correct {
                            background-color: #4541be;
                        }
                        &.positioned {
                            background-color: #50b850;
                        }
                        &.wrong {
                            background-color: #b83c3c;
                        }
                    }
                }
                .response_container {
                    align-items: center;
                    display: flex;
                    flex-direction: row;
                    justify-content: center;

                    div {
                        align-items: center;
                        display: flex;
                        flex-direction: row;
                        justify-content: center;
                        margin: 0 20px;

                        p,
                        span {
                            font-size: 1.3em;
                        }
                    }
                }
            }
        }
    }
    .input_container {
        bottom: 0;
        left: 0;
        padding: 10px;
        position: fixed;
        width: 100%;

        input {
            appearance: textfield;
            -moz-appearance: textfield;
            background-color: #151515;
            border-radius: 5px;
            border: none;
            color: #ccc;
            font-size: 1.3em;
            padding: 10px 0;
            text-align: center;

            &:focus {
                color: #fff;
                outline: none;
            }

            &::-webkit-inner-spin-button,
            &::-webkit-outer-spin-button {
                -webkit-appearance: none;
            }
        }
    }
    .announcer_container {
        background-color: #151515;
        border-radius: 15px;
        left: 50%;
        padding: 10px;
        position: absolute;
        top: 50%;
        transform: translate(-50%, -50%);

        .back_btn {
            background-color: #50b850;
            border-radius: 10px;
            cursor: pointer;
            font-weight: bolder;
            margin: 20px auto;
            padding: 10px;
            width: max-content;
        }
    }
}
</style>
